<script>
import {Carousel, CarouselItem } from 'element-ui';
export default {
    name: 'QunHeYing',
    components: {
        'el-carousel': Carousel,
        'el-carousel-item': CarouselItem
    },
    props: {
        msg: String
    },
    data() {
        return {
            canvasSize: {width: 338, height: 600},
            backImgSize: {width: 1024, height: 3000},
            likeFlag: false,
            title: "",
            useShowArray: [
                '/images/useBack/use_back_1.png',
                '/images/useBack/use_back_2.png',
                '/images/useBack/use_back_3.png',
                '/images/useBack/use_back_4.png'
            ],
            showArray: [
                '/images/showBack/show_1.png',
                '/images/showBack/show_2.png',
                '/images/showBack/show_3.png',
                '/images/showBack/show_4.png',
                '/images/showBack/show_5.png',
                '/images/showBack/show_6.png',
                '/images/showBack/show_7.png',
                '/images/showBack/show_8.jpg',
            ],
            downUrl: "http://qiniu.gafata.com/qunheying-1.0.7.crx",
            chromeUrl: "https://chrome.google.com/webstore/detail/%E5%BE%AE%E4%BF%A1%E7%BE%A4%E5%90%88%E5%BD%B1/okmigchmnpnkhpbadonggpnidnnjocma?hl=zh-CN"
        }
    },
    created() {
        window.addEventListener('scroll', this.handleScroll);
    },
    methods: {
        __getPageScrollY(top) {
            if (top || Number(top) == 0) {
                if (self.pageYOffset) {
                    self.pageYOffset = Number(top);
                }
                if (document.documentElement && document.documentElement.scrollTop) { // Explorer 6 Strict
                    document.documentElement.scrollTop = Number(top);
                }
                if (document.body) {// all other Explorers
                    document.body.scrollTop = Number(top);
                }
                return true;
            } else { //获取垂直滚动值
                var yScroll;
                if (self.pageYOffset) {
                    yScroll = self.pageYOffset;
                } else if (document.documentElement && document.documentElement.scrollTop) { // Explorer 6 Strict
                    yScroll = document.documentElement.scrollTop;
                } else if (document.body) {// all other Explorers
                    yScroll = document.body.scrollTop;
                }
                return yScroll;
            }
        },
        handleScroll: function () {
            let scrollTop = this.__getPageScrollY()
            let obj = document.getElementById("hero")
            let contentHei = obj.offsetHeight - 100
            if (scrollTop > contentHei) {
                let navbar = document.getElementById("navbar")
                navbar.classList.add("navbar_active")
            } else {
                let navbar = document.getElementById("navbar")
                navbar.classList.remove("navbar_active")
            }
        },
        navScroll: function (flag) {
            let fix = 70
            if (flag === 1) {
                let hei = document.getElementById("hero").offsetTop
                window.scrollTo(0,hei - fix);
            } else if (flag === 2) {
                let hei = document.getElementById("main_use").offsetTop
                window.scrollTo(0,hei - fix);
            } else if (flag === 3) {
                let hei = document.getElementById("main_show").offsetTop
                window.scrollTo(0,hei - fix);
            } else if (flag === 4) {
                let hei = document.getElementById("main_install").offsetTop
                window.scrollTo(0,hei - fix);
            } else if (flag === 5) {
                let hei = document.getElementById("main_more").offsetTop
                window.scrollTo(0,hei - fix);
            }
        },
        showLike: function () {
            this.likeFlag = true
        }
    }
}
</script>

